body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.header {
  padding: 10px 0;
}
.message {
  margin: 10px 0;
  padding: 10px;
  font-weight: bold;
  border-top: 5px solid grey;
  border-bottom: 5px solid grey;
}
.message > .body {
  padding: 20px;
  background-color: rgba(127, 127, 100, 0.25);
}
.message > .footer {
  font-size: 0.75em;
}
.comments-container {
  margin-left: 25px;
}
.comment-input {
  width: 50%;
}
.comment {
  font-weight: normal;
  padding-left: 25px;
  font-size: .8em;
}



